import React from 'react';
import {Router, Route, Link, browserHistory} from 'react-router-dom';
import {Row,Col,Card} from 'antd';
import {
    Menu,
    Icon,
    Tabs,
    message,
    Form,
    Input,
    Button,
    CheckBox,
    Modal,
    notification,
    Upload
} from 'antd';

const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;
const FormItem = Form.Item;
const TabPane = Tabs.TabPane;
import PCHeader from './pc_header';
import PCFooter from './pc_footer';

export default class PCUserCenter extends React.Component{
    constructor(){
      super();
      this.state = {
          usercollection : '',
          usercomment :'',
          previewImage : '',
          previewVisible : false
      };
    };
    componentDidMount(){
      var myFetchOptions = {
          method : 'GET'
      };
      fetch("http://newsapi.gugujiankong.com/Handler.ashx?action=getuc&userid="+localStorage.userid,myFetchOptions)
          .then(response=>response.json())
          .then(json=>{
              this.setState({usercollection:json});
          });
      fetch("http://newsapi.gugujiankong.com/Handler.ashx?action=getusercomments&userid="+localStorage.userid,myFetchOptions)
          .then(response=>response.json())
          .then(json=>{
              this.setState({usercomment:json});
          });
    };
    handleCancel(){
        this.setState({previewVisible : false});
    };
    render(){
      const props = {
          action : 'http://newsapi.gugujiankong.com/handler.ashx',
          headers : {
              "Access-Control-Allow-Origin" : "*"
          },
          listType : 'picture-card',
          defaultFileList :[
              {
                  uid : -1,
                  name : 'xxx.png',
                  state : 'done',
                  url : "https://os.alipayobjects.com/rmsportal/NDbkJhpzmLxtPhB.png",
                  thumbUrl : "https://os.alipayobjects.com/rmsportal/NDbkJhpzmLxtPhB.png"
              }
          ],
          onPreview : (file)=>{
              this.setState({previewImage:file.url,previewVisible:true});
          }
      };
      const {usercollection,usercomment} = this.state;
      const usercollectionList = usercollection.length?
          usercollection.map((uc,index)=>(
              <Card key={index} title={uc.uniquekey} extra={<a target="_blank" href={`/12/index.html?_ijt=ip9cf2ttpl50j4o35aotj247gs/#/details/${uc.uniquekey}`}></a>}>
                    <p>{uc.Title}</p>
              </Card>
          ))
          :
      '';
        const usercommentList = usercomment.length?
            usercomment.map((comment,index)=>(
                <Card key={index} title={` ${comment.datetime}  ${comment.uniquekey}`} extra={<a target="_blank" href={`/12/index.html?_ijt=ip9cf2ttpl50j4o35aotj247gs/#/details/${comment.uniquekey}`}></a>}>
                    <p>{comment.Comments}</p>
                </Card>
            ))
            :
            '';
      return(
          <div>
              <PCHeader/>
              <Row>
                  <Col span={2}></Col>
                  <Col span={20}>
                      <Tabs>
                          <TabPane tab="" key="1">
                            <div className="comment">
                                <Row>
                                    <Col span={24}>
                                        {usercollectionList}
                                    </Col>
                                </Row>
                            </div>
                          </TabPane>
                          <TabPane tab="" key="2">
                              <div className="comment">
                                  <Row>
                                      <Col span={24}>
                                          {usercommentList}
                                      </Col>
                                  </Row>
                              </div>
                          </TabPane>
                          <TabPane tab="" key="3">
                            <div className="clearfix">
                                <Upload {...props}>
                                    <Icon type="plus"/>
                                    <div className="ant-upload-text"></div>
                                </Upload>
                                <Modal visible={this.state.previewVisible} footer={null} onCancel={this.handleCancel.bind(this)}>
                                    <img alt="" src={this.state.previewImage} />
                                </Modal>
                            </div>
                          </TabPane>
                      </Tabs>
                  </Col>
                  <Col span={2}></Col>
              </Row>
              <PCFooter/>
          </div>
      );
  };
};


