import React from 'react';
import { Link} from 'react-router-dom';
import {Row,Col} from 'antd';
import {
    Menu,
    Icon,
    Tabs,
    message,
    Form,
    Input,
    Button,
    CheckBox,
    Modal
} from 'antd';

const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;
const FormItem = Form.Item;
const TabPane = Tabs.TabPane;

class PCHeader extends React.Component{
    constructor(){
        super();
        this.state = {
            current : "top",
            modalVisible : false,
            action : "login",
            hasLogined : false,
            userNickName : "",
            userid : 0
        };
    };
    componentWillMount(){
      if(localStorage.userid !== ''){
          this.setState({userid : localStorage.userid,userNickName : localStorage.userNickName});
          this.setState({hasLogined : true});
      }
    };
    setModalVisible(value){
        this.setState({
            modalVisible : value
        });
    };
    handleClick(e){
        if(e.key == "register"){
            this.setState({current : "register"});
            this.setModalVisible(true);
        }else{
            this.setState({current : e.key});
        }
    };
    handleSubmit(e){
        e.preventDefault();
        var myFetchOptions = {
          method : "GET"
        };
        var formData = this.props.form.getFieldsValue();
        console.log(formData);
       fetch("http://newsapi.gugujiankong.com/Handler.ashx?action="+this.state.action
           +"&username="+formData.userName+"&password="+formData.password+"" +
           "&r_userName=" +formData.r_userName+"&r_password="+formData.r_password+"&r_confirmPassword="+formData.r_confirmPassword,myFetchOptions).
            then(response=>response.json()).then(json=>{
                this.setState({
                    userNickName:json.NickUserName,
                    userid:json.UserId
                });
                if(this.state.action == "login"){
                    this.setState({hasLogined:true});
                    localStorage.userid = json.UserId;
                    localStorage.userNickName = json.NickUserName;
                }
                message.success("");
                this.setModalVisible(false);
        });
    };
    callback(key){
        if(key == 1){
            this.setState({action : "login"});
        }else if(key == 2){
            this.setState({action : "register"});
        }
    };
    logout(){
        localStorage.userid = '';
        localStorage.userNickName = '';
        this.setState({hasLogined : false});
    }
    render(){
        let {getFieldDecorator} = this.props.form;
        const userShow  = this.state.hasLogined
            ?<Menu.Item key="logout" class="register">
                <Button type="primary" htmlType="button">{this.state.userNickName}</Button>
                &nbsp;&nbsp;
                <Link target="_blank" to={`/usercenter`}>
                    <Button type="dashed" htmlType="button"></Button>
                </Link>
                &nbsp;&nbsp;
                <Button type="ghost" htmlType="button" onClick = {this.logout.bind(this)}></Button>
            </Menu.Item>
            :
            <Menu.Item key="register" class="register">
                <Icon type="appstore"/>/
            </Menu.Item>
        ;
        return(
            <header>
                <Row>
                    <Col span={2}></Col>
                    <Col span={4}>
                        <a href='/' className="logo">
                            <img src="./src/images/news.png" alt="logo" />
                            <span>ReactNews</span>
                        </a>
                    </Col>
                    <Col span={16}>
                        <Menu selectedKeys={[this.state.current]} mode="horizontal" onClick = {this.handleClick.bind(this)}>
                            <Menu.Item key="top"><Icon type="appstore"/></Menu.Item>
                            <Menu.Item key="shehui"><Icon type="appstore"/></Menu.Item>
                            <Menu.Item key="yule"><Icon type="appstore"/></Menu.Item>
                            <Menu.Item key="tiyu"><Icon type="appstore"/></Menu.Item>
                            <Menu.Item key="guonei"><Icon type="appstore"/></Menu.Item>
                            <Menu.Item key="guoji"><Icon type="appstore"/></Menu.Item>
                            <Menu.Item key="keji"><Icon type="appstore"/></Menu.Item>
                            {userShow}
                        </Menu>
                        <Modal title="" wrapClassName="vertical-center-modal" visible={this.state.modalVisible} onCancel = {()=>this.setModalVisible(false)} onOk = {()=>this.setModalVisible(false)} okText="">
                            <Tabs type="card" onChange = {this.callback.bind(this)}>
                                <TabPane tab="" key="1">
                                    <Form layout="horizontal" onSubmit = {this.handleSubmit.bind(this)}>
                                        <FormItem label="">
                                            {getFieldDecorator('userName')(<Input placeholder=""/>)}
                                        </FormItem>
                                        <FormItem label="">
                                            {getFieldDecorator('password')(<Input type="password" placeholder=""/>)}
                                        </FormItem>
                                        <Button type="primary" htmlType="submit"></Button>
                                    </Form>
                                </TabPane>
                                <TabPane tab="" key="2">
                                    <Form layout="horizontal" onSubmit = {this.handleSubmit.bind(this)}>
                                        <FormItem label="">
                                            {getFieldDecorator('r_userName')(<Input placeholder=""/>)}
                                        </FormItem>
                                        <FormItem label="">
                                            {getFieldDecorator('r_password')(<Input type="password" placeholder=""/>)}
                                        </FormItem>
                                        <FormItem label="">
                                            {getFieldDecorator('r_confirmPassword')(<Input type="password" placeholder=""/>)}
                                        </FormItem>
                                        <Button type="primary" htmlType="submit"></Button>
                                    </Form>
                                </TabPane>
                            </Tabs>
                        </Modal>
                    </Col>
                    <Col span={2}></Col>
                </Row>
            </header>
        );
    };
}

export default PCHeader = Form.create({})(PCHeader);


